
import axios from "axios";
import { ref } from "vue";
import { useRouter } from "vue-router";
import Upload from "@/components/Upload.vue";
export default {
  name: "ProductCreate",
  components: {Upload},
  setup() {
    const title = ref("");
    const description = ref("");
    const image = ref("");
    const price = ref(0);
    const router = useRouter();

    const submit = async () => {
      await axios.post("products", {
        title: title.value,
        description: description.value,
        image: image.value,
        price: price.value,
      });

      await router.push("/products");
    };

    return {
      title,
      description,
      image,
      price,
      submit,
    };
  },
};
