
import { onMounted } from "vue";
import * as c3 from "c3";
import Section from "./Section.vue";
import axios from "axios";
export default {
  name: "Dashboard",
  components: {
    Section,
  },

  setup() {
    onMounted(async () => {
      const chart = c3.generate({
        bindto: "#chart",
        data: {
          x: "x",
          columns: [["x"], ["Sales"]],
          types: {
            Sales: "bar",
          },
        },
        axis: {
          x: {
            type: "timeseries",
            tick: {
              format: "%Y-%m-%d",
            },
          },
        },
      });

      const chart1 = c3.generate({
        bindto: "#chart1",

        data: {
          x: "x",
          columns: [["x"], ["Sales"]],
          types: {
            Sales: "area",
            x: "area-spline",
          },
        },
        axis: {
          x: {
            type: "timeseries",
            tick: {
              format: "%Y-%m-%d",
            },
          },
        },
      });

      const chart2 = c3.generate({
        bindto: "#chart2",

        data: {
          x: "x",
          columns: [["x"], ["Sales"]],
          type: 'spline',
        },
        axis: {
          x: {
            type: "timeseries",
            tick: {
              format: "%Y-%m-%d",
               fit: true
            },
          },
        },
      });

      const res = await axios.get("chart");

      const records = res.data.data;

      chart.load({
        columns: [
          ["x", ...records.map((r: any) => r.date)],
          ["Sales", ...records.map((r: any) => r.sum)],
        ],
      });
      chart1.load({
        columns: [
          ["x", ...records.map((r: any) => r.date)],
          ["Sales", ...records.map((r: any) => r.sum)],
        ],
      });
      chart2.load({
        columns: [
          ["x", ...records.map((r: any) => r.date)],
          ["Sales", ...records.map((r: any) => r.sum)],
        ],
      });
    });
  },
};
